$titleHeight: 77px;
//* listHeight = 100vh - $titleHeight - topMargin(5px)
$listHeight: calc(100vh - 77px - 5px);

.today {
  &__title {
    color: var(--neobisColor);
    font-family: "Montserrat";
    font-size: 30px;
    font-weight: bold;
    text-align: center;
    box-sizing: border-box;
    height: $titleHeight;
    padding: 15px 0 20px;
    @include media(
      (
        font-size: (
          mobile: 23px,
        ),
      )
    );
  }
  &__list {
    min-height: $listHeight;
    background: #f5f7ff;
    border-radius: 10px;
    display: flex;
    width: 98%;
    // margin: 0 0 10px 12px;
    margin: 0 auto 10px;
    flex-direction: column;
    box-sizing: border-box;
    padding: 5px 10px 10px 10px;
    &-item {
      display: flex;
      width: 100%;
      height: 80px;
      background: rgba(250, 255, 254, 0.8);
      box-sizing: border-box;
      margin: 5px 0 5px 0;
      cursor: pointer;
      &-answer {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 90px;
        height: 100%;
        flex-shrink: 0;
        @include media(
            (
              width: (
                mobile: 70px,
              ),
            )
          );
          &_circle {
          width: 34px;
          height: 34px;
          border-radius: 50%;
          background-color: var(--neobisColor);
          &_red {
            width: 34px;
            height: 34px;
            border-radius: 50%;
            background-color: #eb0f0f !important;
          }
        }
      }
      &-content {
        display: flex;
        flex-direction: column;
        box-sizing: border-box;
        font-family: "Montserrat";
      }
      &-text {
        padding: 16px 0 5px 0;
        font-size: 22px;
        font-weight: bold;
        width: 100%;
        max-height: 32px;
        color: rgba(33, 51, 54, 0.8);
        overflow-x: auto;
        @include media(
          (
            font-size: (
              mobile: 18px,
            ),
            line-height: (
              mobile: 1,
            ),
            max-height: (
              tablet: 26px,
              mobile: 32px,
            ),
          )
        );
      }
      &-time {
        font-size: 14px;
        color: #4f4f4f;
      }
    }
  }
}
